@import 'config';

div {
    border: 1px solid #cccccc;
    box-sizing: border-box;
}



@mixin flex-box($align, $jusfity, $direction: row, $wrap: wrap) {
	display: flex;
	align-items: $align;
	justify-content: $justify;
	flex-direction: $direction;
	flex-wrap: $wrap;
}



html, body {
    padding: 0; margin: 0;
}

header {
    width: 100%; height: 45px;
    display: flex;
    justify-content: space-between;
}